.wrapper {
  height: 100%;
  background-color: #F7F7F7;
}
.scheduleServe {
  padding-top: 62px;
  background: #f2f2f2;
}
.desc {
  color: #ccc;
  font-style: 14px;
}
.desc .people {
  color: #000;
  font-size: 16px;
}
.desc .tips {
  color: #ff953c;
  font-size: 14px;
}
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}
.body {
  background: #fff;
  padding: 16px 30px;
}
.body .title {
  width: 6rem;
}
.body .title .btn {
  float: right;
  color: #FF4463;
  font-weight: normal;
  border: 1px solid #FF4463;
  margin-right: 8px;
  padding: 0 10px;
  border-radius: 50px ;
}
.body .title .yelBtn {
  color: #f90;
  border: 1px solid #f90;
}
.fr {
  float: right;
}
.checkIcon {
  color: #349ce1;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #ccc;
  overflow: hidden;
  align-content: center;
  line-height: 20px;
  display: inline-block;
  margin-right: 3%;
  margin-left: 5%;
}
.labels label {
  width: 40%;
  display: inline-block;
}
.historyTop {
  background: #fff;
  padding: 8px 16px;
  font-size: 16px;
}
.historyTop .icon {
  color: #999;
  font-weight: normal;
  margin-top: -8px;
}
.mb_8 {
  margin-bottom: 8px;
}
.detailFooter {
  text-align: center;
  background: #ff953c;
  line-height: 1rem;
  color: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1rem;
  font-size: 0.4rem;
}
